.popup-subscription { --bgColor: #000000; --textColor: #fff; --themeColor: #230CEA; position: fixed; right: 0; bottom: 0; width: 375px; background: var(--bgColor); z-index: 11; color: var(--textColor); display: flex; flex-direction: column; transition: all ease 0.2s; opacity: 0; visibility: hidden; }

